import React from 'react';
import TelegramMessage from './TelegramMessage/TelegramMessage';
import s from './TelegramMessage/TelegramMessage.module.css'

function HW1() {
    return (
        <div className={s.container}>
            <TelegramMessage userName={'Master Oogway'}
                             avatarURL={'data:image/jpeg;base64,'}
                             userMessage={'Случайности не случайны'}
                             messageTime={'20:00'}
            />
            <TelegramMessage userName={'Po'}
                             avatarURL={'https://ichef.bbci.co.uk/news/1024/media/images/77396000/jpg/_77396213_kung-fu-panda-21.jpg'}
                             userMessage={'За свою улетность денег не беру, а за красоту тем более!'}
                             messageTime={'20:35'}
            />
            <TelegramMessage userName={'Master Shifu'}
                             avatarURL={'data:image/jpeg;base64,'}
                             userMessage={'По пельмешке?'}
                             messageTime={'20:38'}
            />
        </div>
    )
}

export default HW1;